:root {
    --background: white;
    --text-primary: black;
}

[data-theme='dark'] {
    --background: black;
    --text-primary: white;
}

body,
html {
    transition: 600ms;
    background-color: var(--background);
    overflow-x: hidden;
    margin: 0;
    padding: 0;
}

#name {
    font-weight: 700;
    user-select: none;
    font-size: 21pt;
    transition: 600ms;
    color: var(--text-primary);
}

canvas {
    transition: all 600ms;
}

nav ul {
    margin: 0;
    padding: 0;
}

nav {
    margin: auto;
    width: 100%;
    max-width: 800px;
    overflow: hidden;
    list-style: none;
    padding: 0;
    text-align: center;
    padding-bottom: 2px;
}

nav li {
    display: inline-block;
    border-radius: 10px;
    width: 130px;
    padding: 0 12px;
    user-select: none;
}

nav a {
    font-size: 15pt;
    text-decoration: none;
    color: var(--text-primary);
    user-select: none;
    transition: 600ms;
}

#icons {
    padding: 0;
    margin: 10px;
    /* position: fixed;  
    left: 0px;  
    bottom: 5px;  
    right: 0px; */
    text-align: center;  
}

#icons a {
    padding: 15px;
    margin: auto;
    text-decoration: none;
    text-align: center;
    width: 10%;
}

#icons i {
    font-size: 32px;
    width: 32px;
    transition: 600ms;
    color: var(--text-primary);
}

#content {
    padding: 0%;
    line-height: 2;
    text-align: center;
    align-items: center;
    transition: 600ms;
    color: var(--text-primary);
    font-size: 20px;
}

#text {
    display: inline-block;
    max-width: 900px;
    padding: 0 5px;
    font-size: calc(0.8rem + 0.3vw);
    text-align: center;
}

input {
    z-index: -2
}

#buttons {
    text-align: center;
    margin: 10px 0 5px 0;
}

.controls {
    border-radius: 8px;
    border-width: 0;
    font-size: 14px;
    line-height: 20px;
    margin: auto;
    padding: 5px 12px;
    text-align: center;
    max-width: 600px;
    display: inline;
    overflow: hidden;
    list-style: none;
}

select {
    border-radius: 8px;
    text-align: center;
    font-size: 14px;
    margin: 0 5px 0;
    padding: 5px 0;
}

.box {
    margin: 15px auto 10px;
    display: flex;
}

.center {
    flex: 1;
    display: flex;
    justify-content: center;
    margin-left: 45px;
}

#switch {
    color: var(--text-primary);
    background-color: transparent;
    border: none;
    display: inline-block;
    padding: 0;
    margin: 5px 20px 5px 0px;
    height: 25px;
    width: 25px;
    border-radius: 50%;
    text-align: right;
    font-size: 25px;
    transition: color 500ms;
    cursor: pointer;
    touch-action: manipulation;
    -webkit-tap-highlight-color: transparent;
    outline-offset: 5px;
}

.links {
    text-decoration: none;
    color: var(--text-primary);
    transition: 600ms;
}

img {
    padding: 15px 0 5px 0;
    max-width: 60%;
    max-height: 500px;
    animation: fadeIn ease 2s;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.centered {
    height: 85vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--text-primary);
}